import { AES, SHA256 } from "crypto-js";
import { BXUser } from "../types/bitrix/bitrix-type";
import encUtf8 from 'crypto-js/enc-utf8';


export function getDepartmentKey() {
  const today = new Date();
  const formattedDate = `${today.getMonth() + 1}${today.getDate()}${today.getFullYear()}`;
  return `department_${formattedDate}`;
}

// Функция для сохранения данных в LocalStorage
export function saveDepartmentToLocalStorage(users: Array<BXUser>, key: string, domain: string) {
  const encryptedUsers = AES.encrypt(JSON.stringify(users), domain).toString(); // Шифруем объект пользователей
  localStorage.setItem(key, JSON.stringify({
    data: encryptedUsers,
    timestamp: new Date().toISOString() // Сохраняем текущую дату/время
  }));

}

// Функция для проверки наличия данных в LocalStorage
export function getDepartmentFromLocalStorage(key: string, domain: string) {
  const storedData = localStorage.getItem(key);
  if (storedData) {
    const parsedData = JSON.parse(storedData);
    const storedDate = new Date(parsedData.timestamp);

    // Проверяем, если данные старше одного дня, то они устарели
    const oneDay = 24 * 60 * 60 * 1000; // Один день в миллисекундах
    if ((new Date().getTime() - storedDate.getTime()) > oneDay) {
      return null; // Данные устарели
    }
    const decryptedData = AES.decrypt(parsedData.data, domain).toString(encUtf8); // Расшифровываем данные
    try {
      const decryptedData = AES.decrypt(parsedData.data, domain).toString(encUtf8); // Расшифровываем данные
      return JSON.parse(decryptedData); // Возвращаем расшифрованный объект пользователей
    } catch (e) {
      console.error('Error decrypting data:', e);
      return null; // Ошибка при расшифровке
    }
  }
  return null; // Данных нет
}

// Основная функция для работы с API и LocalStorage
